import {PostData} from './post_data';
export class Users {
    _id: String;
    info: Info = new Info();
    address_residence: AddressResidence = new AddressResidence();
    contact: Contact = new Contact();
    password: String;
    image: String;
    create_at: Date;
    last_update: Date;
    apply: [PostData];
    provider:String;
    id:String;
    constructor() { };
}
class Info {
    name: String;
    first_name: String;
    last_name: String;
    full_name: String;
    gender: String;
    dob: Date;
    employment_sitution: String;
    areas_expertise: String;
    constructor(){}
}
class AddressResidence {
    address: String;
    latitude: String;
    longitude: String;
    city: String;
    district: String;
    ward: String;
    constructor(){}
}
class Contact {
    email: String;
    phone: String;
    web_page: String;
    constructor(){}
}



