import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
//import * as io from 'socket.io-client';

import { SocketService } from './socket.service';
import { appConfig } from '../app.config';

@Injectable()
export class CommentSocketService {

  private socket;
  constructor(
    private mSocket: SocketService
  ) {
    this.socket = this.mSocket.getSocket();
  }
  addComment(data) {
    this.socket.emit('add-comment', data);
  }
  addReply(data) {
    this.socket.emit('add-reply', data);
  }
  join(room) {
    this.socket.emit('join-room', room);
  }
  getCommentMessages() {
    let observable = new Observable(observer => {
      this.socket.on('comment-message', (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }
  getReplyMessage() {
    let observable = new Observable(observer => {
      this.socket.on('reply-message', (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }
}



