export class Gender{
    Female: any = 0;
    Male: any = 1;
    Other: any = 2;
    arr: [String] = ['female', 'male', 'other'];
    compare(g: String): String{
            return this.arr.indexOf(g.toLocaleLowerCase())+'';
    }
    constructor(){};
}


